@keyframes float-main {
  0% {
    transform: translateX(0px) translateY(0px) rotate(0deg) scale(1);
  }
  25% {
    transform: translateX(8px) translateY(-4px) rotate(-1.5deg) scale(1.01);
  }
  50% {
    transform: translateX(16px) translateY(0px) rotate(1.5deg) scale(1.02);
  }
  75% {
    transform: translateX(6px) translateY(3px) rotate(-0.75deg) scale(1.01);
  }
  100% {
    transform: translateX(0px) translateY(0px) rotate(0deg) scale(1);
  }
}
@keyframes float-soft {
  0% {
    transform: translateX(0px) translateY(0px) rotate(0deg) scale(1);
  }
  25% {
    transform: translateX(-6px) translateY(2px) rotate(1deg) scale(1.005);
  }
  50% {
    transform: translateX(-12px) translateY(0px) rotate(-1deg) scale(1.01);
  }
  75% {
    transform: translateX(-4px) translateY(-2px) rotate(0.5deg) scale(1.005);
  }
  100% {
    transform: translateX(0px) translateY(0px) rotate(0deg) scale(1);
  }
}
.header-floating {
  position: sticky;
  top: 12px;
  z-index: 30;
  padding-inline: 1rem;
  background: transparent;
  border-bottom: none;
  backdrop-filter: none;
  isolation: isolate;
}
.header-floating-inner {
  position: relative;
  z-index: 1;
  max-width: 1120px;
  margin-inline: auto;
  padding: 0.7rem 1.5rem;
  border-radius: 9999px;
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0.97),
    rgba(248, 250, 252, 0.97)
  );
  border: 1px solid rgba(226, 232, 240, 0.95);
  box-shadow: 0 18px 45px rgba(15, 23, 42, 0.18);
  backdrop-filter: blur(10px);
}
.header-banner {
  position: absolute;
  top: 50%;
  width: 230px;
  height: 90px;
  border-radius: 9999px;
  filter: blur(30px);
  opacity: 0.8;
  pointer-events: none;
  transform: translateY(-50%);
  z-index: 0;
}
.header-banner-1 {
  left: -70px;
  background: linear-gradient(90deg, #ef4444, #f97316);
}
.header-banner-2 {
  right: -80px;
  background: linear-gradient(90deg, #0ea5e9, #22c55e);
}
@media (max-width: 640px) {
  .header-floating {
    top: 8px;
    padding-inline: 0.5rem;
  }
  .header-floating-inner {
    padding: 0.55rem 0.9rem;
    border-radius: 9999px;
    box-shadow: 0 12px 30px rgba(15, 23, 42, 0.16);
  }
  .header-banner {
    width: 150px;
    height: 70px;
    filter: blur(24px);
    opacity: 0.7;
  }
  .header-banner-1 {
    left: -40px;
  }
  .header-banner-2 {
    right: -45px;
  }
}
.social-icons {
  display: flex;
  flex-wrap: wrap;
  gap: 0.55rem;
  list-style: none;
  align-items: center;
}
.social-icons li {
  position: relative;
  flex-basis: 2.1rem;
}
.social-icons li.whatsapp {
  --bg: #25d366;
}
.social-icons li.instagram {
  --bg: linear-gradient(
    -45deg,
    #feda75,
    #fa7e1e,
    #d62976,
    #962fbf,
    #4f5bd5
  );
}
.social-icons li.facebook {
  --bg: #1877f2;
}
.social-icons li::after {
  position: absolute;
  content: attr(data-tooltip);
  inset: -38% auto auto 50%;
  translate: -50%;
  padding: 0.15rem 0.55rem;
  font-size: 0.65rem;
  color: #fff;
  background: var(--bg, #070707);
  border-radius: 0.25rem;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: 0.25s ease-in-out;
}
.social-icons li:has(a:hover)::after,
.social-icons li:has(a:focus-visible)::after {
  opacity: 1;
  visibility: visible;
  inset-block-start: -55%;
}
.social-icons a {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  aspect-ratio: 1;
  width: 2.1rem;
  height: 2.1rem;
  border: 1px solid #e5e7eb;
  border-radius: 9999px;
  color: #1f2937;
  overflow: hidden;
  transition: border-color 0.25s ease-in-out;
}
.social-icons a svg {
  width: 1rem;
  height: 1rem;
}
.social-icons a::after {
  content: "";
  position: absolute;
  inset: 100% 0 0;
  background: var(--bg);
  transition: inset 0.25s ease-in-out;
  z-index: -1;
}
.social-icons a:hover,
.social-icons a:focus-visible {
  border-color: transparent;
}
.social-icons a:hover::after,
.social-icons a:focus-visible::after {
  inset-block-start: 0;
}
@media (max-width: 640px) {
  .social-icons {
    justify-content: center;
  }
}
